<template>
  <div>
    <fTitle>
      <template #title>Table 表格</template>
      <template #introduce>用于展示多条结构类似的数据，可对数据进行排序、筛选、对比或其他自定义操作。</template>
    </fTitle>
    <sTitle>
      <template #title>表格demo1</template>
      <template #introduce>筛选表格的用法</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
      <el-button @click="resetDateFilter">清除日期过滤器</el-button>
  <el-button @click="clearFilter">清除所有过滤器</el-button>
        <el-table
      :data="tableData"
      border
      max-height="250"
      :row-class-name="tableRowClassName"
      ref="singleTable"
      highlight-current-row
      :default-sort = "{prop: 'date', order: 'descending'}"
      @current-change="handleCurrentChange"
      style="width: 100%">
      <el-table-column
      type="selection"
      width="55">
    </el-table-column>
      <el-table-column
      type="index"
      width="50">
    </el-table-column>
      <el-table-column
        prop="date"
        label="日期"
        sortable
        width="180"
        column-key="date"
        :filters="[{text: '2016年', value:1},{text: '2016年之前', value:2},{text: '2016年之后', value:3}]"
      :filter-method="filterHandler">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址">
      </el-table-column>
      <el-table-column
        prop="tag"
        label="标签"
        :filters="[{ text: '家', value: '家' }, { text: '公司', value: '公司' }]"
      :filter-method="filterTag">
        <template slot-scope="scope">
        <el-tag
          :type="scope.row.tag === '家' ? 'primary' : 'success'"
          disable-transitions>{{scope.row.tag}}</el-tag>
      </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 20px">
    <el-button @click="setCurrent(tableData[1])">选中第二行</el-button>
    <el-button @click="setCurrent()">取消选择</el-button>
  </div></el-col>
  </el-row>

    </div>
    </div>
    <sTitle>
      <template #title>表格demo2</template>
      <template #introduce>自定义表格的用法</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
      <el-table
      :data="tableData2"
      max-height="250"
      border
      style="width: 100%">
      <el-table-column
        prop="id"
        label="id"
        fixed
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="180">
        <template slot-scope="scope">
        <el-popover trigger="hover" placement="top">
          <p>姓名: {{ scope.row.name }}</p>
          <p>住址: {{ scope.row.address }}</p>
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </el-popover>
      </template>
      </el-table-column>
      <el-table-column
        prop="amount1"
        label="数据1"
        width="180">
      </el-table-column>
      <el-table-column
        prop="amount2"
        label="数据2"
        width="180">
      </el-table-column>
      <el-table-column
        prop="amount3"
        label="数据3"
        width="180">
      </el-table-column>
      <el-table-column
      fixed="right"
      label="操作"
      width="100">
      <template slot-scope="scope">
        <el-button @click="handleClick1(scope.row)" type="text" size="small">删除</el-button>
        <el-button  @click="handleClick2(scope.row)" type="text" size="small">编辑</el-button>
      </template>
    </el-table-column>
    </el-table>
    </el-col></el-row>
    </div></div>
    <el-dialog style="text-align: start;" width="600px" :title="'请正在编辑的id为:'+ form.eId" :visible.sync="dialogFormVisible">
  <el-form :model="form">
    <el-form-item label="输入姓名">
      <el-input v-model="form.name" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="输入地址">
      <el-input v-model="form.address" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="输入数据1">
      <el-input v-model="form.num1" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="输入数据2">
      <el-input v-model="form.num2" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="输入数据3">
      <el-input v-model="form.num3" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="sureChange">确 定</el-button>
  </div>
</el-dialog>
  </div>

</template>

