  },
  methods: {
    sureChange () {
      this.tableData2.find(item => item.id === this.form.eId).name = this.form.name
      this.tableData2.find(item => item.id === this.form.eId).address = this.form.address
      this.tableData2.find(item => item.id === this.form.eId).amount1 = this.form.num1
      this.tableData2.find(item => item.id === this.form.eId).amount2 = this.form.num2
      this.tableData2.find(item => item.id === this.form.eId).amount3 = this.form.num3
      this.dialogFormVisible = false
      this.form = {
        name: '',
        address: '',
        num1: '',
        num2: '',
        num3: '',
        eId: ''
      }
    },
    handleClick1 (row) {
      this.tableData2 = this.tableData2.filter(item => item.id !== row.id)
    },
    handleClick2 (row) {
      this.dialogFormVisible = true
      this.form.eId = row.id
    },
    tableRowClassName ({ row, rowIndex }) {
      if (+row.date.slice(0, 4) < 2016) {
        return 'warning-row'
      } else if (+row.date.slice(0, 4) > 2016) {
        return 'success-row'
      }
      return ''
    },
    setCurrent (row) {
      this.$refs.singleTable.setCurrentRow(row)
    },
    handleCurrentChange (val) {
      this.currentRow = val
      console.log(val)
    },
    resetDateFilter () {
      this.$refs.singleTable.clearFilter('date')
    },
    clearFilter () {
      this.$refs.singleTable.clearFilter()
    },
    filterTag (value, row) {
      return row.tag === value
    },
    filterHandler (value, row, column) {
      if (value === 1) {
        return +row.date.slice(0, 4) === 2016
      }
      if (value === 2) {
        return +row.date.slice(0, 4) < 2016
      }
      if (value === 3) {
        return +row.date.slice(0, 4) > 2016
      }

      // const property = column.property
      // return row[property] === value
    }
  }
